export default (update => {
  let lastUpdate = Date.now();
  let fps = 0;
  let running = true;
  let requestId;

  (function loop() {
    if (running) {
      const delta = Date.now() - lastUpdate;
      lastUpdate = Date.now();
      fps = 1000 / delta;
      update(delta / 1000);
      requestId = requestAnimationFrame(loop);
    }
  })();

  return {
    isRunning: () => running,
    start: () => {
      if (!running) {
        running = true;
        loop();
      }
    },
    stop: () => {
      if (requestId) {
        cancelAnimationFrame(requestId);
      }

      running = false;
    },
    getFps: () => fps
  };
});