import { mat4 } from "gl-matrix";
import { unproject } from "./mathUtils";
export default ((width, height, viewportWidth, viewportHeight) => {
  const combined = mat4.create();
  mat4.ortho(combined, 0, width, height, 0, 0, 1);
  const invOrtho = mat4.create();
  mat4.invert(invOrtho, combined);
  const transform = mat4.create();
  const tmp3 = [0, 0, 0];
  const origin = [];
  const scaleVec = [];
  const negOriginVec = [];
  const translateVec = [];
  return {
    combined,

    setMatrix(mat) {
      mat4.copy(combined, mat);
      mat4.invert(invOrtho, combined);
    },

    unprojectVector2(out, input) {
      tmp3[0] = input.x;
      tmp3[1] = input.y;
      tmp3[2] = 0;
      unproject(tmp3, tmp3, invOrtho, 0, 0, viewportWidth, viewportHeight);
      out.set(tmp3[0], tmp3[1]);
    },

    resize(width, height) {
      mat4.ortho(combined, 0, width, height, 0, 0, 1);
      mat4.invert(invOrtho, combined);
    },

    translate(x, y) {
      translateVec.length = 0;
      translateVec[0] = x;
      translateVec[1] = y;
      translateVec[2] = 0;
      mat4.translate(combined, combined, translateVec);
      mat4.invert(invOrtho, combined);
    },

    scale(originX, originY, scale) {
      origin.length = 0;
      origin[0] = originX;
      origin[1] = originY;
      origin[2] = 0;
      scaleVec.length = 0;
      scaleVec[0] = scale;
      scaleVec[1] = scale;
      scaleVec[2] = 1;
      negOriginVec.length = 0;
      negOriginVec[0] = -originX;
      negOriginVec[1] = -originY;
      negOriginVec[2] = 0;
      mat4.identity(transform);
      mat4.translate(transform, transform, origin);
      mat4.scale(transform, transform, scaleVec);
      mat4.translate(transform, transform, negOriginVec);
      mat4.mul(combined, combined, transform);
      mat4.invert(invOrtho, combined);
    }

  };
});